$breakpoints: (
    "small": 480px,
    "medium": 720px,
    "large": 960px,
    "extralarge": 1200px,
);

@mixin small {
    @media (min-width: map-get($breakpoints, "small")) {
        @content;
    }
}
@mixin medium {
    @media (min-width: map-get($breakpoints, "medium")) {
        @content;
    }
}
@mixin large {
    @media (min-width: map-get($breakpoints, "large")) {
        @content;
    }
}
@mixin extralarge {
    @media (min-width: map-get($breakpoints, "extralarge")) {
        @content;
    }
}